@import "library/github-calendar";
@import "library/github-calendar-responsive";
@import "font-awesome/scss/font-awesome";
@import "ed-grid/ed-grid";

$color1: rgba(73, 81, 90, .8);
$color2: rgba(73, 81, 90, 1);
$rosado: #ce5f5f;
$body-font: Lato, 'sans-serif';
$heading-font: 'Open Sans', 'sans-serif';

// Font sizes
$fontSizes: (
  t1      : rem(36px),
  t2      : rem(30px),
  t3      : rem(24px),
  normal  : rem(20px),
  small   : rem(16px),
  smaller : rem(14px),
);

// Get font size from map
@function fontSize($size) {
  @if map-has-key($fontSizes, $size) {
    @return map-get($fontSizes,$size)
  } @else {
    @error '$size is not valid, valid values are t1, t2, t3, normal, small, smaller'
  }
}

* {
	font-family: $body-font;
	font-size: fontSize(small);
	color: #49515a;
}

body 
{
	background-color: rgba(70, 130, 180, 0.12156862745098039);
}
.texto {
	padding-left: .5em;
}

.img_profile
{
	background: url('../img/profile.jpg') no-repeat center;
	background-position: 0 0;
	background-size: cover;
	border: 1px solid $color1;
	height: 12rem;
	width: 12rem;
	background-position: top center;
}

.nombre_completo h1
{
	font-family: $heading-font;
	font-size: fontSize(normal);
	font-weight : 700;
}

.profesion h2 
{
	font-family: $body-font;
	font-size: fontSize(small);
	line-height: 0;
}

.redes_item
{
	background-color: rgba(73, 81, 90, 0.62);
	border-radius: 50%;
	height: 32px;
	margin-right:.5em;
	padding: .5em;
	transition: ease-out all .3s;
	width: 32px;
}

.redes_item:last-child
{
	margin-right: 0;
}

.redes_item:hover 
{
	background-color:  steelblue;
	text-decoration: none;
}

.redes_item i 
{
	color: #fff;
}

.boton_enviar
{
	background-color: $color1 !important;
	font-weight: bold;
	transition: ease-out all .3s;
	text-align: center;
}

.boton_enviar:hover
{
	background-color: $color2 !important;
}

.boton_enviar 
{
	i, span 
	{
		color: #fff;
		font-size: fontSize(smaller);
		font-weight: 700;
	}
}

.color 
{
	height: .5rem;
}

.colores_footer > .color
{
	height: 2.5rem;
}

.color-1
{
	background-color: $color2;
}

.color-2
{
	background-color: $rosado;
}

.color-3
{
	background-color: $color2;
}

.color-4
{
	background-color: $rosado;
}

.color-5
{
	background-color: $color2;
}

.parrafo
{
	text-align: justify;
	line-height: 1.5;
}

.contenedor
{
	margin-top: 2rem;
}

.item_contenido, .item_aside
{
	background: #FFF;
	font-size: 1.1em;
}

.item_contenido
{
	margin-bottom: 1rem;
}

.item_aside
{
	margin-bottom: 1rem;
}

.item_contenido .titulo, .item_aside .titulo
{
	font-family: $heading-font;
	font-size: fontSize(normal);
	font-weight : 700;
}

.icono_grado,
.icono_trabajo,
.icono_proyecto
{
	width: 32px;
	display: inline-block;
	margin: 0;
}

.grado_datos,
.trabajo_datos,
.proyecto_datos
{
	display: inline-block;
	vertical-align: bottom;
	max-width: 80%;
}

.grado_datos > span,
.trabajo_datos > span,
.proyecto_datos > span
{
	display: block;
}

.grado_datos .grado,
.trabajo_datos .cargo,
.proyecto_datos .institucion
{
	font-family: $body-font;
	font-size: fontSize(small);
	font-weight : 700;
}

.grado_datos .universidad .nombre_universidad,
.trabajo_datos .institucion .nombre_institucion,
.proyecto_datos .lugar_proyecto .nombre_lugar
{
	color: rgba(73, 81, 90, .4);
	font-family: $body-font;
	font-size: fontSize(smaller);
	font-weight : 700;
}

.grado_datos .universidad .tiempo_universidad,
.trabajo_datos .institucion .tiempo_trabajo,
.proyecto_datos .lugar_proyecto .tiempo_trabajo
{
	color: rgba(73, 81, 90, .4);
	font-family: $body-font;
	font-size: fontSize(smaller);
}

.feed ul
{
	list-style-image: url("../img/icon-feed.png");
	margin: 0;
	margin-bottom: 1.1rem;
}

.feed li .headline
{
	display: inline-block;
	text-align: justify;
	max-width: 90%;
	font-weight: bold;
	vertical-align: top;
}

.feed li
{
	margin-bottom: 1.1rem;
}

.feed li > div
{
	display: none;
}


.skill
{
	background: #f5f5f5;
	box-shadow: inset 0 1px 2px rgba(0,0,0,.1);
	margin-bottom: 1rem;
	border-radius: 5px;
	position: relative;
}

.skill:last-child
{
	margin-bottom: 1.7rem;
}

.skill_html,
.skill_css,
.skill_js,
.skill_git,
.skill_php,
.skill_net,
.skill_java,
.skill_cpp,
.skill_sqlserver,
.skill_mysql
{
	border-radius: 5px;
}

.skill_html
{
	width: 90%;
	background: $rosado;
}

.skill_css
{
	width: 80%;
	background: #3b658a;
}

.skill_js
{
	width: 90%;
	background: #249991
}

.skill_git
{
	width: 90%;
	background: $color2;
}

.skill_php
{
	width: 90%;
	background: $rosado;
}

.skill_net
{
	width: 90%;
	background: #3b658a;
}

.skill_java
{
	width: 70%;
	background: #249991
}

.skill_cpp
{
	width: 55%;
	background: $color2;
}

.skill_sqlserver
{
	width: 80%;
	background: $rosado;
}

.skill_mysql
{
	width: 80%;
	background: #3b658a;
}

.skill .nombre_skill
{
	color: rgba(255,255,255,.9);
	font-weight: bold;
	font-size: .9em;
	display: inline-block;
	background: rgba(0,0,0,.2);
	padding: .4rem .5rem;
	border-radius: 5px 0 0 5px;
}

.skill .competado_skill
{
	position: absolute;
	right: 8px;
	top: 50%;
	transform: translateY(-50%);
	color: rgba(73, 81, 90, .4);
	font-weight: bold;
}

.cargando
{
	text-align: center;
	margin-top: 1rem;
}

.calendar
{
	box-sizing: border-box;
	width: 100%;
	margin-bottom: 1.2rem;
	border: none;
}

.texto_footer
{
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	background: rgba(0,0,0,0);
	text-align: center;
	bottom: 0;
}

.texto_footer .texto
{
	color: rgba(255,255,255,.9);
	display: inline-block;
	margin-top: .75rem;
	font-family: $heading-font;
	font-size: fontSize(small);
	font-weight : 700;
}
.texto_footer .texto i
{
	color: rgba(255,255,255,.8);
}

#cabezera
{
	background-color: #fff;
	padding: 1em 0;
}

#datos-contancto
{
	padding: 1em 0 ;
}

#datos-contancto, #educacion,
#trabajos, #proyectos
{
	i 
	{
		width: 32px;
		text-align: center;
		line-height: 2;
		color: rgba(73, 81, 90, .3);
		vertical-align: middle;
	}
}

#datos-contancto a
{
	font-weight: bold;
}

#educacion .grado_academico,
#trabajos .item_trabajo,
#proyectos .item_proyecto
{
	margin-bottom: 1rem;
}

#educacion .grado_academico:last-child,
#trabajos .item_trabajo:last-child,
#proyectos .item_proyecto:last-child
{
	margin-bottom: 1.7rem;
}

#blog_feed 
{
	overflow: hidden;
	transition: ease-in all .3s;
}

#creditfooter
{
	display: none;
}

#pie
{
	margin-top: 1rem;
	position: relative;
}

@media all and (max-width:640px)
{
	.nombre_completo h1
	{
		line-height: 1;
		text-align: center;
	}

	.profesion, .nombre_completo, .redes_sociales
	{
		display:flex;
		flex-wrap:wrap;
		justify-content:center;
	}

	.grado_datos, .trabajo_datos,
	.proyecto_datos
	{
		vertical-align: middle;
	}

	.redes_sociales
	{
		margin: 1.5em 0;
	}
	.item_aside
	{
		margin-bottom: .5rem;
		margin-top: 1rem;
	}
}